import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { BlogService } from '../blog.service';

//import { ToastsManager } from 'ng2-toastr/ng2-toastr';


@Component({
  selector: 'app-post-blog',
  templateUrl: './postblog.component.html',
  styleUrls: ['./postblog.component.css'],
  providers: [Location]
})
export class PostblogComponent implements OnInit {

  constructor(private route: ActivatedRoute,private router: Router, private blogService: BlogService, private blogservice: BlogService) {

    //this.toastr.setRootViewContainerRef(vcr);

   }

  public blogTitle: string;
  public blogBodyHtml: string;
  public blogDescription: string;
  public blogCategory: string;
  public possibleCategories = ["Comedy", "Drama", "Action", "Technology"];

  ngOnInit() {
  }

  createBlog(): any {

    let blogData = {

      title : this.blogTitle,
      description : this.blogDescription,
      blogBody : this.blogBodyHtml,
      category : this.blogCategory

    }// end blog data

    console.log(blogData);

    this.blogService.createBlog(blogData).subscribe(

      data => {
        console.log("Blog Created")
        console.log(data);
        alert('Blog Posted successfully');
        setTimeout(()=>{  
          this.router.navigate(['/blog',data.data.blogId]);
        }, 700)
        
        

      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
        alert("erroroccured");
      }


    )


  }// end create blog function

}
