import { Component, OnInit,ViewContainerRef } from '@angular/core';
import { BlogService } from '../blog.service';
import { ActivatedRoute,Router } from '@angular/router';
//import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { Location } from '@angular/common';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css'],
  providers: [Location]
})
export class ViewComponent implements OnInit {

      public currentBlog;
  constructor(private blogservice:BlogService,private router:Router,private route:ActivatedRoute,private location:Location,vcr: ViewContainerRef ){ 
  console.log("view constructor is called");
  //this.toastr.setRootViewContainerRef(vcr);

  }

  ngOnInit() {
    let myBlogId = this.route.snapshot.paramMap.get('blogId');
    console.log(myBlogId)
    //this.currentBlog = this.blogService.getSingleBlog(myBlogId);
    this.blogservice.getSingleBlog(myBlogId).subscribe(

      data => {
        console.log(data);
        this.currentBlog = data["data"];

      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }


    )
  }

  deleteThisBlog(): any {

    this.blogservice.deleteBlog(this.currentBlog.blogId).subscribe(

      data => {
        console.log(data);
        //this.toastr.success('Blog Deleted successfully', 'Success!');
        alert("Blog Deletion successfull");
        setTimeout(() => {
          this.router.navigate(['/home']);
        }, 1000)

      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
        alert('Some error occured');
      }


    )




  }// end delete this blog 

  goBackToPreviousPage(): any {

    this.location.back();

  }



    }
  


